import React, { Component } from "react";

class HeaderOptionIcon extends Component {
  render() {
    const { nameIcon } = this.props;
    // console.log(nameIcon);
    return (
      <div className="header-option">
        {nameIcon.map((item, i) => (
          <a href="#" key={i}>
            <i className={item}></i>
          </a>
        ))}
      </div>
    );
  }
}

export default HeaderOptionIcon;
