import React, { Component } from "react";
import "./sidebar-left.css";

class LeftItem extends Component {
  render() {
    console.log(this.props.itemLists);
    const mainLists = this.props.itemLists.mainLists;
    const racourcis = this.props.itemLists.racourcis;
    const parcourir = this.props.itemLists.parcourir;
    // console.log(mainLists);
    // console.log(racourcis);
    // console.log(parcourir);

    return (
      <ul className="sidebar-left">
        {mainLists.map((item, i) => (
          <a href="#" key={i}>
            <i className={item}></i>
            <li>{item}</li>
          </a>
        ))}

        {racourcis.map((item, i) => (
          <a href="#" key={i}>
            <i className={item}></i>
            <li>{item}</li>
          </a>
        ))}
        {parcourir.map((item, i) => (
          <a href="#" key={i}>
            <i className={item}></i>
            <li>{item}</li>
          </a>
        ))}
      </ul>
    );
  }
}

export default LeftItem;
