import React, { Component } from "react";
import LeftItem from "./LeftItem";

class LeftSideContain extends Component {
  render() {
    const styles = {
      marginleft: "200px",
      width: "250px",
      padding: "10px",
      height: "100%",
      marginTop: "30px",

      position: "fixed",
      zIndex: "1",
      top: "0",
      left: "0",

      overflowX: "hidden",
      paddingTop: "20px",
    };
    const tab = ["1", "2", "3"];
    const itemLists = {
      mainLists: ["Fil d'actualit√©", "Messenger", "Watch", "Marketplace"],
      racourcis: ["game poker", "Black desert"],
      parcourir: ["Covid-19", "Video en direct", "Enregistrements"],
    };

    return (
      <div style={styles}>
        <LeftItem itemLists={itemLists} />
      </div>
    );
  }
}

export default LeftSideContain;
