import React, { Component } from "react";
import avatar from "../../img/avatar.jpg";
import "./Section.css";

class SectionHeader extends Component {
  render() {
    const styles = {
      display: "flex",
      marginBottom: "20px",
    };
    return (
      <div className="section-header" style={styles}>
        <img src={avatar} alt="" />
        <h2>Title</h2>
      </div>
    );
  }
}

export default SectionHeader;
