import React, { Component } from "react";
import SectionBody from "./SectionBody";

import SectionHeader from "./SectionHeader";

class SectionContain extends Component {
  render() {
    const styles = {
      width: "50%",
      padding: "20px",
      marginLeft: "25%",
      backgroundColor: "white",
    };
    return (
      <div style={styles}>
        <SectionHeader />
        <SectionBody />
        <SectionBody />
        <SectionBody />
        <SectionBody />
        <SectionBody />
      </div>
    );
  }
}

export default SectionContain;
