import React, { Component } from "react";
import HeaderMenuSingle from "./HeaderMenuSingle";
import "./Header.css";

class HeaderMenu extends Component {
  render() {
    const menu = ["username", "Acceuil", "Retrouvez des amis", "Cr√©er"];
    const styles = {
      width: "40%",
      color: "#fffffa",
      fontSize: "12px",
      fontWeight: "bold",
      verticalAlign: "top",
      whiteSpace: "nowrap",
      marginTop: "4px",
    };

    return (
      <div style={styles}>
        <HeaderMenuSingle menu={menu} />
      </div>
    );
  }
}

export default HeaderMenu;
