import React, { Component } from "react";
import HeaderOptionIcon from "./HeaderOptionIcon";

class HeaderOption extends Component {
  render() {
    const nameIcon = [
      "fa fa-user-friends",
      "fab fa-facebook-messenger",
      "fa fa-bell",
      "fa fa-question circle",
    ];
    const styles = {
      display: "flex",
      marginTop: "10px",
      width: "20%",
    };
    return (
      <div style={styles}>
        <HeaderOptionIcon nameIcon={nameIcon} />
      </div>
    );
  }
}

export default HeaderOption;
