import React, { Component } from "react";

class RightSideContain extends Component {
  render() {
    const styles = {
      width: "250px",
      padding: "10px",
      height: "100vh",
      marginTop: "120px",
      position: "fixed",
      zIndex: "1",
      top: "0",
      right: "0",
      overflowX: "hidden",
      paddingTop: "20px",
    };
    return <div style={styles}>hello from right side</div>;
  }
}

export default RightSideContain;
