import React, { Component } from "react";
import "./Header.css";

class HeaderMenuSingle extends Component {
  render() {
    const { menu } = this.props;

    console.log(menu[0]);
    const styles = {
      textDecoration: '"none"',
      padding: "0 5px",
      borderRight: "1px solid #3b5ca0",
    };

    return (
      <ul className="header-menu">
        {menu.map((item, i) => (
          <li key={i} style={styles}>
            <a href="#"> {item}</a>
          </li>
        ))}
      </ul>
    );
  }
}

export default HeaderMenuSingle;
