import React, { Component } from "react";
import post from "../../img/post.jpg";
import "./Section.css";

class SectionBody extends Component {
  render() {
    return (
      <div className="section-body">
        <img src={post} alt="" />
        <h2>description post</h2>
      </div>
    );
  }
}

export default SectionBody;
